@import url('https://fonts.googleapis.com/css2?family=Baloo+Tamma+2&family=Roboto&display=swap');

div {
  // background-color: #fafafa;
  font-family: 'Roboto', sans-serif;
}
body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  overflow: hidden;
  box-sizing: border-box;
}

$taskItemTitleColor: rgba(15, 134, 179, 1);
$taskItemBorderColor: rgba(228, 228, 228, 1);
$taskItemBackgroundColor: rgba(255, 253, 204, 0.82);
$taskItemTextColor: gray;
$taskItemTitleDoneColor: gray;

#root {
  .container {
    display: grid;
    background-color: white;
    grid-template-areas:
      'title header header header'
      'side main main main';
    grid-template-rows: 10vh 90vh;
    grid-template-columns: minmax(280px, 1fr) 3fr;
    grid-gap: 1px;
    .todo-title {
      &h3 {
        grid-area: title;
        text-align: center;
      }
    }
    .todos-container {
      background-color: rgba(204, 218, 222, 1);
      grid-area: side;
      display:grid;
      grid-template-rows: 10fr 1fr;
      grid-template-columns: minmax(250px, 100%);
      .todos-items{
        overflow-y:auto;
        .todos-card{
          margin: 10px;
          border: 1px solid whitesmoke;
          background-color: #cff1ff;
          padding: 10px;
          border-radius: 5px;
          cursor: pointer;
          &.active{
            border: 4px solid #70a0d0;
            background-color: #95c0d6;
          }
        }
      }
      .todos-footer {
        display: flex;
        justify-content: center;
        z-index: 50;
        margin: 5px;
        & button {
          width: 100%;
          border-radius: 5px;
          background: rgba(51, 96, 255, 1);
          color: white;
          outline-style: revert;
          outline-width: thin;
          border-width: 0;
          font-size: 1.2em;
          transition: 0.3s;
          &:hover {
            box-shadow: 0px 0px 9px 2px #009aec;
          }
          &:active {
            background-color: #1b458c;
          }
        }
      }
    }
    .tasks-header {
      padding: 10px;
      margin: auto 40px;
      overflow: hidden;
      grid-area: header;
      background-color: rgba(250, 250, 250, 1);
    }
    .tasks-table {
      padding: 10px 0;
      grid-area: main;
      background-color: rgba(239, 248, 251, 1);
      display: flex;
      flex-direction: row;
      justify-content: center;
      flex-wrap: wrap;
      width: 100%;
      overflow-y: auto;
      .task-item {
        min-width: 200px;
        max-width: 300px;
        height: 200px;
        border: 1px solid $taskItemBorderColor;
        background-color: $taskItemBackgroundColor;
        border-radius: 5px 20px 5px 5px;
        color: $taskItemTextColor;
        display: grid;
        grid-template-rows: 4fr 1fr;
        grid-gap: 1px;
        text-align: center;
        box-shadow: 0 0 10px 0px #d8d8d8;
        margin: 10px;
        padding:1em;
        cursor: pointer;
        & p:nth-child(1) {
          overflow-y: auto;
          color: $taskItemTitleColor;
          font-size: 1em;
        }
        & p:nth-child(2) {
          font-size: 0.9em;
          border-top: 1px solid $taskItemBorderColor;
          padding-top: 10px;
          font-style: italic;
        }
        &.done .task-title {
          text-decoration-line: line-through;
          color: $taskItemTitleDoneColor;
        }
        &.done{
          background-color: white;
          opacity: 0.3;
        }
        &:hover{
        }
        animation-name: fadeIn;
        animation-duration: 0.5s;
        @media screen and (max-width: 500px){
          max-width:80%;
          min-width:250px;
        }
      }
    }
    //-------------------------------------
    
    @media screen and (max-width: 500px) {
      .todo-button{
        display:block;
        position: fixed;
        top:50vh;
        left:-25px;
        width:50px;
        height:80px;
        background-color: #05383866;
        border-radius: 20px;
        box-shadow: 0 0 5px black;
        z-index:80;
        transition: 0.5s;
        &:hover{
          box-shadow: 0 0 20px black;
        } 
      }
      .back-font{
        position:fixed;
        top:0;
        left:0;
        width:100vw;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.5);
        z-index: 10;
        &.fadeout{
          transform:translate(-100vw);
          opacity: 0;
          animation-name: fadeOut;
          animation-duration: 0.5s;
        }
        &.fadein{
          opacity:1;
          animation-name: fadeIn;
          animation-duration: 1s;
        }
      }
      grid-template-areas: 'header' 'main';
      grid-template-columns: 1fr;
      grid-template-rows: 10vh 90vh; 
      .todo-title{
        display:none;
      }
      .todos-container{
        position:fixed;
        top:0;
        left:0;
        width:300px;
        height:100vh;
        z-index:50;
        &.show{
          animation-name: slideIn;
          animation-duration: 0.3s;
        }
        &.hide{
          transform:translateX(-300px);
          opacity:0;
          animation-name: slideOut;
          animation-duration: 0.3s;
        }
        
      }

    }
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeOut{
  0%{opacity: 1;transform:translate(0);}
  100%{opacity: 0;transform:translate(0);}
}
@keyframes slideIn{
  0%{transform:translateX(-300px);opacity:0;}
  100%{opacity:1;}
}
@keyframes slideOut{
  0%{transform:translateX(0);opacity:1;}
  100%{transform:translateX(-300px);opacity:1;}
}
