@import url(https://fonts.googleapis.com/css2?family=Baloo+Tamma+2&family=Roboto&display=swap);
div{font-family:'Roboto', sans-serif}body{margin:0;padding:0;font-size:16px;overflow:hidden;box-sizing:border-box}#root .container{display:grid;background-color:white;grid-template-areas:'title header header header' 'side main main main';grid-template-rows:10vh 90vh;grid-template-columns:minmax(280px, 1fr) 3fr;grid-gap:1px}#root .container .todo-titleh3{grid-area:title;text-align:center}#root .container .todos-container{background-color:#ccdade;grid-area:side;display:grid;grid-template-rows:10fr 1fr;grid-template-columns:minmax(250px, 100%)}#root .container .todos-container .todos-items{overflow-y:auto}#root .container .todos-container .todos-items .todos-card{margin:10px;border:1px solid whitesmoke;background-color:#cff1ff;padding:10px;border-radius:5px;cursor:pointer}#root .container .todos-container .todos-items .todos-card.active{border:4px solid #70a0d0;background-color:#95c0d6}#root .container .todos-container .todos-footer{display:flex;justify-content:center;z-index:50;margin:5px}#root .container .todos-container .todos-footer button{width:100%;border-radius:5px;background:#3360ff;color:white;outline-style:revert;outline-width:thin;border-width:0;font-size:1.2em;transition:0.3s}#root .container .todos-container .todos-footer button:hover{box-shadow:0px 0px 9px 2px #009aec}#root .container .todos-container .todos-footer button:active{background-color:#1b458c}#root .container .tasks-header{padding:10px;margin:auto 40px;overflow:hidden;grid-area:header;background-color:#fafafa}#root .container .tasks-table{padding:10px 0;grid-area:main;background-color:#eff8fb;display:flex;flex-direction:row;justify-content:center;flex-wrap:wrap;width:100%;overflow-y:auto}#root .container .tasks-table .task-item{min-width:200px;max-width:300px;height:200px;border:1px solid #e4e4e4;background-color:rgba(255,253,204,0.82);border-radius:5px 20px 5px 5px;color:gray;display:grid;grid-template-rows:4fr 1fr;grid-gap:1px;text-align:center;box-shadow:0 0 10px 0px #d8d8d8;margin:10px;padding:1em;cursor:pointer;-webkit-animation-name:fadeIn;animation-name:fadeIn;-webkit-animation-duration:0.5s;animation-duration:0.5s}#root .container .tasks-table .task-item p:nth-child(1){overflow-y:auto;color:#0f86b3;font-size:1em}#root .container .tasks-table .task-item p:nth-child(2){font-size:0.9em;border-top:1px solid #e4e4e4;padding-top:10px;font-style:italic}#root .container .tasks-table .task-item.done .task-title{-webkit-text-decoration-line:line-through;text-decoration-line:line-through;color:gray}#root .container .tasks-table .task-item.done{background-color:white;opacity:0.3}@media screen and (max-width: 500px){#root .container .tasks-table .task-item{max-width:80%;min-width:250px}}@media screen and (max-width: 500px){#root .container{grid-template-areas:'header' 'main';grid-template-columns:1fr;grid-template-rows:10vh 90vh}#root .container .todo-button{display:block;position:fixed;top:50vh;left:-25px;width:50px;height:80px;background-color:#05383866;border-radius:20px;box-shadow:0 0 5px black;z-index:80;transition:0.5s}#root .container .todo-button:hover{box-shadow:0 0 20px black}#root .container .back-font{position:fixed;top:0;left:0;width:100vw;height:100vh;background-color:rgba(0,0,0,0.5);z-index:10}#root .container .back-font.fadeout{transform:translate(-100vw);opacity:0;-webkit-animation-name:fadeOut;animation-name:fadeOut;-webkit-animation-duration:0.5s;animation-duration:0.5s}#root .container .back-font.fadein{opacity:1;-webkit-animation-name:fadeIn;animation-name:fadeIn;-webkit-animation-duration:1s;animation-duration:1s}#root .container .todo-title{display:none}#root .container .todos-container{position:fixed;top:0;left:0;width:300px;height:100vh;z-index:50}#root .container .todos-container.show{-webkit-animation-name:slideIn;animation-name:slideIn;-webkit-animation-duration:0.3s;animation-duration:0.3s}#root .container .todos-container.hide{transform:translateX(-300px);opacity:0;-webkit-animation-name:slideOut;animation-name:slideOut;-webkit-animation-duration:0.3s;animation-duration:0.3s}}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@-webkit-keyframes fadeOut{0%{opacity:1;transform:translate(0)}100%{opacity:0;transform:translate(0)}}@keyframes fadeOut{0%{opacity:1;transform:translate(0)}100%{opacity:0;transform:translate(0)}}@-webkit-keyframes slideIn{0%{transform:translateX(-300px);opacity:0}100%{opacity:1}}@keyframes slideIn{0%{transform:translateX(-300px);opacity:0}100%{opacity:1}}@-webkit-keyframes slideOut{0%{transform:translateX(0);opacity:1}100%{transform:translateX(-300px);opacity:1}}@keyframes slideOut{0%{transform:translateX(0);opacity:1}100%{transform:translateX(-300px);opacity:1}}

.modal-background{position:fixed;width:100%;height:100%;left:0;top:0;background-color:rgba(0,0,0,0.7);z-index:100;-webkit-animation-name:fadeIn;animation-name:fadeIn;-webkit-animation-duration:0.5s;animation-duration:0.5s}.modal-input-group{font-size:1em;display:flex;justify-content:center;flex-direction:column;width:400px;min-width:160px;background-color:white;padding:20px;position:fixed;top:calc((50vh / 2) + 20px);left:calc(50vw - (440px / 2));z-index:200;opacity:1;box-shadow:0 0 50px 0px black;-webkit-animation-name:zoomIn;animation-name:zoomIn;-webkit-animation-duration:0.5s;animation-duration:0.5s}@media screen and (max-width: 500px){.modal-input-group{width:220px;top:calc((50vh / 2) + 20px);left:calc(50vw - (220px /2) - 20px);font-size:0.8em}}.modal-input-group h3{color:#456565}.modal-input-group .modal-input{padding:30px;height:100px}.modal-input-group .modal-input input{width:98%;font-size:1.8em;border:none;background-color:transparent;border-bottom:1px solid rgba(0,157,208,0.2);color:#565656}.modal-input-group .modal-input input::-webkit-input-placeholder{color:#dedede}.modal-input-group .modal-input input:-ms-input-placeholder{color:#dedede}.modal-input-group .modal-input input::-ms-input-placeholder{color:#dedede}.modal-input-group .modal-input input::placeholder{color:#dedede}.modal-input-group .modal-input input:focus{outline:none;border-bottom:2px solid #009dd0;-webkit-animation-name:inputBorderFadeIn;animation-name:inputBorderFadeIn;-webkit-animation-duration:0.2s;animation-duration:0.2s;-webkit-animation-timing-function:linear;animation-timing-function:linear}.modal-input-group .modal-input label{color:rgba(6,6,6,0.62);font-size:0.8em}.modal-input-group .modal-input-footer{margin-top:20px;display:grid;grid-template-columns:1fr 1fr;grid-template-rows:1fr;grid-gap:10px}.modal-input-group h3{text-align:center;font-size:1.8em}.modal-input-group button{background-color:lightblue;height:2em;outline-style:revert;outline-width:thin;border-width:0;font-size:1.1em;color:#025b90}.modal-input-group button:hover{box-shadow:0 0 5px rgba(0,0,0,0.5)}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@-webkit-keyframes zoomIn{0%{transform:scale(0.3);opacity:0}100%{transform:scale(1);opacity:1}}@keyframes zoomIn{0%{transform:scale(0.3);opacity:0}100%{transform:scale(1);opacity:1}}@-webkit-keyframes inputBorderFadeIn{0%{border-bottom:1px solid rgba(0,157,208,0.2)}100%{border-bottom:2px solid #009dd0}}@keyframes inputBorderFadeIn{0%{border-bottom:1px solid rgba(0,157,208,0.2)}100%{border-bottom:2px solid #009dd0}}

